import React, { useState, useEffect, useRef } from 'react'
import { useSelector } from 'react-redux';
import TreeView from 'deni-react-treeview';
import callApi from '../../../helpers/conectorApi';
import Loading from '../base/Loading'
import Aux from '../../../hoc/_Aux';
import { Card, Row } from 'react-bootstrap';
import { Col } from 'react-bootstrap';
import { NoAutorizado } from '../base/NoAutorizado';
const menuId = 40;
const menuIdCuentaContable = 41;
export const GrupoContableListar = () => {
    const [loading, setLoading] = useState(true);
    const state = useSelector(state => state);
    const [accesos, setAccesos] = useState([]);
    const [catGrupoContable, setCatGrupoContable] = useState([]);
    const [catCuentasContables, setCatCuentasContables] = useState([]);
    const [dataArbol, setDataArbol] = useState([]);
    const [armarArbol, setArmarArbol] = useState(false);
    const treeviewRef = useRef(null);
    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdCuentaContable);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }
    const GetCuentas = (grupo_contableId) => {
        let cuentasAux = [];
        let gruposAux = [];
        cuentasAux = catCuentasContables.filter(i => Number(i.grupo_contableId) === Number(grupo_contableId));
        gruposAux = catGrupoContable.filter(i => Number(i.padre_grupo_contableId) === Number(grupo_contableId));

        if (cuentasAux) {
            let cuentas = [];
            cuentasAux.map(({ cuenta_contableId, codigo, nombre }) => {
                cuentas.push({
                    id: cuenta_contableId,
                    text: `${codigo} - ${nombre}`,
                    isLeaf: true
                });
            });

            gruposAux.map(({ grupo_contableId, codigo, descripcion, padre_grupo_contableId }) => {
                cuentas.push({
                    id: codigo,
                    text: `${codigo} - ${descripcion}`,
                    children: GetCuentas(grupo_contableId)
                });
            });

            return cuentas;
        }

    }

    const armarDatArbol = () => {
        let padres = [];
        padres = catGrupoContable.filter(i => i.padre_grupo_contableId === null)

        let listArbol = [];
        if (padres) {
            padres.map(({ grupo_contableId, codigo, descripcion, padre_grupo_contableId }) => {
                listArbol.push({
                    id: codigo,
                    text: `${codigo} - ${descripcion}`,
                    children: GetCuentas(grupo_contableId)
                });
            });
        }
        setDataArbol(listArbol);
    }
    const GetGrupoContable = async () => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(true);
            let responseGrupo = await callApi(`grupocontable?include=0&estadoId=1`);
            if (responseGrupo) {
                setCatGrupoContable(responseGrupo);
                GetCuentasContables();
            }
            setLoading(false);
        } else {
            setCatGrupoContable([{ nombre: 'No esta autorizado' }]);
        }
    }

    const GetCuentasContables = async () => {
        if (accesos.find(acceso => acceso.menuId === menuIdCuentaContable && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi(`cuentacontable?include=0&estado=1`);
            if (response) {
                setCatCuentasContables(response);
                setArmarArbol(true);
            }
            setLoading(false);
        } else {
            setCatCuentasContables([{ nombre: 'No esta autorizado' }]);
        }
    }


    useEffect(() => {
        GetAccesosByMenuId();
    }, [])

    useEffect(() => {
        GetGrupoContable();
    }, [accesos]);

    // useEffect(() => {
    //     GetCuentasContables();
    // }, [catGrupoContable]);


    useEffect(() => {
        armarDatArbol();
    }, [armarArbol])


    return (
        accesos.length > 0 &&
        <Aux>
            <Row className='btn-page'>
                <Col sm={12}>
                    <Card>
                        <Card.Header>
                            <Card.Title as="h5">Arbol de Cuentas</Card.Title>
                        </Card.Header>
                        <Card.Body>
                            {
                                loading === true ?
                                    <Loading />
                                    : accesos.find(i => i.accesoId === 3) ?
                                        <TreeView
                                            ref={treeviewRef}
                                            className="customizable-tree"
                                            items={dataArbol}
                                            theme="orange"
                                            showRoot={false}
                                            showCheckbox={false}
                                            marginItems="40"
                                            selectRow={true}
                                        /> : <NoAutorizado />

                            }
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>

    )
}
